@import url(https://fonts.googleapis.com/css?family=Rubik&display=swap);
* {
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.app{
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
}

header{
    min-height: 70px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
}
header .logo img{
    min-width:100%;
    min-height: 100%;
    width: 150px;
}
header nav{
    display: flex;
}
nav ul li{
    list-style: none;
    display: inline-block;
}
nav ul li a{
    text-decoration: none;
    text-transform: uppercase;
    color: #555;
    padding: 0 15px;
    font-size: 17px;
}
nav ul li a:hover{
    color: lightseagreen;
}
nav .nav-cart{
    cursor: pointer;
    position: relative;
}
nav .nav-cart span{
    position: absolute;
    top:-12px;
    right: -7px;
    background: crimson;
    font-size: 10px;
    color: white;
    padding: 3px 5px;
    border-radius: 60%;
    z-index: -1;
}
header .menu,
header .close{
    cursor: pointer;
    display: none;
}
@media (max-width: 650px){
    header ul{
        position: fixed;
        top:0;
        left: -100%;
        width: 100%;
        height:70vh;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
        background: white;
        z-index: 99;
        opacity: 0.97;
        transition: 0.5s linear;
    }
    header .menu,
    header .close{
        display: block;
    }
    header .close{
        position: absolute;
        top:10px;
        right: 20px;
    }
    header ul.toggle{
        left: 0;
    }
    header .logo img{
        min-width:100%;
        min-height: 100%;
        width: 150px;
    }
}
#product{
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.card{
    min-width: 100px;
    min-height: 40px;
    border: 1 solid gray;
    overflow: hidden;
    padding: 25px;
    box-shadow: 2px 8px 20px rgb(2, 1, 1);
    margin: 4px;
    transition: 0.5s linear;
    background-color: black;
}
.card:hover{
    box-shadow: none;
}
.card img{
    align-items: center;
    position: relative;
    width: 150px !important;
    height: 300px !important;
    text-decoration: none;
    text-align: center;
}
.card h3{
    font-size: 15px !important;
    text-align: center;
}
.card h3 a{
    text-decoration: none;
    color: rgb(236, 232, 232);
}
.card h3 a:hover{
    color: crimson;
}
.card span{
    color: crimson;
}
.card p{
    margin: 10px 0;
}
.card button{
    border: none;
    outline: none;
    background: #333;
    color: rgb(2, 2, 2);
    width: 100%;
    height: 40px;
    display: block;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 15px 0;
}
.details{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 50px 0;
}
.details img{
    max-width: 500px;
    min-width: 290px;
    height: 300px;
    display: block;
    object-fit: cover;
    margin: 25px;
}
.details .box{
    max-width: 500px;
    min-width: 290px;
    margin: 25px;
}
.box .row{
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}
.box .row h2{
    text-transform: uppercase;
    letter-spacing: 2px;
}
.box .row span{
    color: crimson;
}
.box .colors button{
    width: 30px;
    height: 30px;
    border: 1px solid #333;
    outline: none;
    margin-right: 5px;
    cursor: pointer;
}
.box p{
    line-height: 1.5;
    margin: 15px 0;
}
.box .cart{
    background: #333;
    color: rgb(3, 3, 3);
    text-decoration: none;
    padding: 10px 25px;
    display: inline-block;
}
section .cart{
    position: relative;
    border: 1px solid rgb(0, 0, 0);
}
.amount span{
    color: crimson;
    padding: 0 20px;
}
.amount .count{
    width: 40px;
    height: 30px;
    border: 1px solid #555;
    outline: none;
    background: transparent;
    cursor: pointer;
}
.delete{
    position: absolute;
    top:0;
    right: 5px;
    color: crimson;
    cursor: pointer;
    font-weight: 900;
}
.total{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.total a{
    text-decoration: none;
    background: #333;
    color: rgb(15, 14, 14);
    text-transform: uppercase;
    letter-spacing: 2px;
    padding: 10px 25px;
}
.total h3{
    margin-right: 50px;
    color: crimson;
}
.brands_image img{
   min-width: 100%;
   min-height: 100%;
   width: 300px;
   height: 450px;
   left: 300px !important;
}
.brands_description h1{
    color: white;
    top: 100px;
    text-align: center;
    font-size: 60px;
    position: relative;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.brands_description h3{
    color: white;
    text-align: center;
    position:relative;
    margin-top: 150px;
    font-size: 40px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;

}
.brands_description p{
    color: rgb(109, 106, 106);
    text-align: center;
    position: relative;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
span{
    font-size: 18px;
    color:cadetblue
}
.brands_description{
   padding: 10px;  
   align-self: center;
}
 small {
     font-size: calc(12px + (15 - 12) * ((100vw - 360px) / (1600 - 360))) !important
 }

 .bold-text {
     color: #989c9e !important
 }


 h3 {
     font-size: calc(34px + (40 - 34) * ((100vw - 360px) / (1600 - 360))) !important
 }

 .social {
     font-size: 21px !important
 }

 .rights {
     font-size: calc(10px + (12 - 10) * ((100vw - 360px) / (1600 - 360))) !important
 }
